export enum ValueType {
  eq = 'ValueType.eq',
  ne = 'ValueType.ne',
  gt = 'ValueType.gt',
  lt = 'ValueType.lt',
  ge = 'ValueType.ge',
  le = 'ValueType.le',
  between = 'ValueType.between',
  like = 'ValueType.like',
  in = 'ValueType.in', //
  nin = 'ValueType.nin', //
}

export const singleValuetype = [
  ValueType.eq,
  ValueType.ne,
  ValueType.gt,
  ValueType.ge,
  ValueType.lt,
  ValueType.le,
  ValueType.like
];

export const mutilValueType = [ ValueType.between, ValueType.in, ValueType.nin ];

export const valueTypeMaps: {[key: string]: Array<ValueType>} = {
  enum: [ ValueType.eq, ValueType.ne, ValueType.in, ValueType.nin ],
  email: [ ValueType.eq, ValueType.ne, ValueType.like ],
  text: [ ValueType.eq, ValueType.ne, ValueType.like ],
  textarea: [ ValueType.eq, ValueType.ne, ValueType.like ],
  markdown: [ ValueType.eq, ValueType.ne, ValueType.like ],
  url: [ ValueType.eq, ValueType.ne, ValueType.like ], // url
  count: [
    // 
    ValueType.eq,
    ValueType.ne,
    ValueType.gt,
    ValueType.ge,
    ValueType.lt,
    ValueType.le,
    ValueType.between
  ],
  numeric: [
    // 
    ValueType.eq,
    ValueType.ne,
    ValueType.gt,
    ValueType.ge,
    ValueType.lt,
    ValueType.le,
    ValueType.between
  ],
  refer: [ ValueType.eq, ValueType.ne, ValueType.in, ValueType.nin ], //  
  bool: [ ValueType.eq, ValueType.ne ], // bool
  datetime: [
    // 
    ValueType.eq,
    ValueType.ne,
    ValueType.gt,
    ValueType.ge,
    ValueType.lt,
    ValueType.le,
    ValueType.between,
    ValueType.in,
    ValueType.nin
  ],
  date: [
    // 
    ValueType.eq,
    ValueType.ne,
    ValueType.gt,
    ValueType.ge,
    ValueType.lt,
    ValueType.le,
    ValueType.between,
    ValueType.in,
    ValueType.nin
  ]
};

export interface IvalueTypeOption {
  value: ValueType;
  label: string;
}
export const valueTypeOptions = [
  {
    value: ValueType.eq,
    label: ''
  },
  {
    value: ValueType.ne,
    label: ''
  },
  {
    value: ValueType.gt,
    label: ''
  },
  {
    value: ValueType.lt,
    label: ''
  },
  {
    value: ValueType.ge,
    label: ''
  },
  {
    value: ValueType.le,
    label: ''
  },
  {
    value: ValueType.between,
    label: ''
  },
  {
    value: ValueType.like,
    label: ''
  },
  {
    value: ValueType.in,
    label: ''
  },
  {
    value: ValueType.nin,
    label: ''
  }
];

export const getValueTypeOptions = (type: string): Array<IvalueTypeOption> => {
  const types = valueTypeMaps[ type ];
  if (!types) {
    return [];
  }

  return types.map((type) => valueTypeOptions.find((option) => option.value === type));
};
