import * as React from 'react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import remarkMath from 'remark-math';
import rehypeKatex from 'rehype-katex';
import rehypeRaw from 'rehype-raw';
import {Prism as SyntaxHighlighter} from 'react-syntax-highlighter';
import {a11yDark} from 'react-syntax-highlighter/dist/esm/styles/prism';
import EazyWin from 'vwin-eazy-win';
import ImageLoader from 'vwin-image-loader';
import './style.scss';

interface IMarkdownViewer {
  value: string;
}

export const MarkdownViewer = (props: IMarkdownViewer) => {
  return (
    <ReactMarkdown
      className="markdown-viewer"
      children={props.value}
      remarkPlugins={[remarkGfm, remarkMath]}
      // remarkPlugins={[remarkMath]}
      rehypePlugins={[rehypeRaw]}
      components={{
        code: ({node, inline, className, children, ...reset}) => {
          const match = /language-(\w+)/.exec(className || '');
          return !inline && match ? (
            //@ts-ignore
            <SyntaxHighlighter
              children={String(children).replace(/\n$/, '')}
              style={a11yDark}
              language={match[1]}
              PreTag="div"
              {...reset}
            />
          ) : (
            <code className={className} {...reset}>
              {children}
            </code>
          );
        },
        img: ({node, ...reset}) => (
          <EazyWin theme="dark">
            <ImageLoader {...reset as any} />
          </EazyWin>
        )
      }}
    />
  );
};
