export class StringUtils {
  // 
  static lowerCapital (str: string) {
    if (typeof str !== 'string') {
      return str;
    }

    return str[ 0 ].toLowerCase() + str.substring(1);
  }

  static upperCapital (str: string) {
    if (typeof str !== 'string') {
      return str;
    }

    return str[ 0 ].toUpperCase() + str.substring(1);
  }
}
