import { ModalProps } from "antd";
import * as React from "react";
import { v4 as uuidv4 } from "uuid";

export interface ModalsComponentProps {
  onOk?: (...data: any) => void;
  onCancel?: (...data: any) => void;
  visible?: boolean;
}

export interface IModals extends ModalsComponentProps{
  [key:string]: any;
}

interface ModalsState {
  modals: Array<{
    id: string;
    Comp: React.Component<any> | React.FC<any> | React.ReactNode; // propsModal
    props: {
      onOk: (data?: any) => void;
      onCancel: (data?: any) => void;
      visible: boolean;
      [key:string]: any;
    } & IModals;
  }>;
}

export class Modals extends React.Component<any, ModalsState> {
  static install: Modals;

  constructor(props: any) {
    super(props);
    if (Modals.install) {
      return Modals.install;
    }
    Modals.install = this;

    this.state = {
      modals: [],
    };
  }

  public static show = (
    component: React.Component<any> | React.FC<any> | React.ReactNode,
    props: IModals = {}
  ) => {
    const { modals = [] } = Modals.install.state;
    return new Promise((resolve, reject) => {
      const id = uuidv4();
      modals.push({
        id,
        Comp: component,
        props: {
          ...props,
          visible: true,
          onOk: (...data) => {
            props.onOk && props.onOk(data);
            Modals.install.onClose(id);
          },
          onCancel: (...data) => {
            props.onCancel && props.onCancel(data);
            Modals.install.onClose(id);
          },
        },
      });
      Modals.install.setState({});
    });
  };

  public static clear() {
    if (Modals.install) {
      Modals.install.state.modals.forEach(modal => {
        Modals.install.onClose(modal.id);
      })
    }
  }

  /**
   * @param id 
   */
  private onClose = (id: string) => {
    const { modals = [] } = this.state;
    this.setState({
      modals: modals.filter((modal) => modal.id !== id),
    });
  };

  render() {
    const { modals = [] } = this.state;
    return (
      <div>
        {modals.map((item) => {
          const Comp: any = item.Comp;
          return <Comp key={item.id} {...item.props} />;
        })}
      </div>
    );
  }
}
