import * as React from 'react';
import {Image} from 'antd';
import {IBizFormComponentOProps} from '../interface';

export interface IImageViewer {
  value: string;
}

export const ImageViewer = (props: IImageViewer) => {
  return (
    <Image
      width={100}
      height={100}
      src={props.value}
      fallback="data:image/png;base64,"
    />
  );
};
